import "./RoleCreateInfoInput.scss";

import React from "react";
import PropTypes from "prop-types";

function RoleCreateInfoInput(props) {
  const { value, hasDice } = props;

  let dice = null;
  if (hasDice) {
    dice = (
      <button type="button" className="button-invisible RoleCreateInfoInput__Dice d-none">
        &nbsp;
      </button>
    );
  }

  return (
    <div className="position-relative">
      <input value={value} className="w-100 RoleCreateInfoInput text-dark fs-6" disabled />
      {dice}
    </div>
  );
}

RoleCreateInfoInput.propTypes = {
  value: PropTypes.string,
  hasDice: PropTypes.bool,
};

RoleCreateInfoInput.defaultProps = {
  value: "",
  hasDice: true,
};

export default RoleCreateInfoInput;
