import "./WoodenButton.scss";

import React from "react";
import PropTypes from "prop-types";

function WoodenButton(props) {
  const {
    children,
    variant,
    color,
    onClick,
    className,
    py,
  } = props;

  return (
    <button
      type="button"
      className={`
        WoodenButton
        WoodenButton--variant-${variant}
        text-${color}
        py-${py}
        fw-bold
        ${className}
      `}
      onClick={onClick}
    >
      {children}
    </button>
  );
}

WoodenButton.propTypes = {
  children: PropTypes.string,
  color: PropTypes.string,
  variant: PropTypes.number,
  onClick: PropTypes.func,
  className: PropTypes.string,
  py: PropTypes.number,
};

WoodenButton.defaultProps = {
  children: "",
  color: "black",
  variant: 0,
  onClick: () => {},
  className: "",
  py: 3,
};

export default WoodenButton;
