import "./RoleManageAttribute.scss";

import React from "react";
import PropTypes from "prop-types";

import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

import AttributeStrength from "../../../assets/attribute-strength.svg";
import AttributeAgile from "../../../assets/attribute-agile.svg";
import AttributeIntelligence from "../../../assets/attribute-intelligence.svg";
import AttributeCharm from "../../../assets/attribute-charm.svg";
import AttributeEndurance from "../../../assets/attribute-endurance.svg";

function getIcon(title) {
  switch (title) {
    case "Strength":
      return AttributeStrength;
    case "Agile":
      return AttributeAgile;
    case "Intelligence":
      return AttributeIntelligence;
    case "Charm":
      return AttributeCharm;
    case "Endurance":
      return AttributeEndurance;
    default:
      return "";
  }
}

function RoleManageAttribute(props) {
  const {
    title, color, value, total, className,
  } = props;
  return (
    <Row style={{ color }} className={`mx-0 ${className}`}>
      <Col
        xs="auto"
        className="position-relative d-flex justify-content-center align-items-center"
      >
        <svg viewBox="0 0 26 36" className="RoleManageAttribute__Shield">
          <path d="M26 0H0V27.7714L13.2549 36L26 27.7714V0Z" fill={color} />
        </svg>
        <img
          src={getIcon(title)}
          alt={title}
          className="RoleManageAttribute__Icon"
        />
      </Col>
      <Col className="pb-3">
        <Row className="fs-sm">
          {title}
        </Row>
        <Row className="fs-sm flex-nowrap">
          <div
            style={{
              background: color,
              width: `${(100 * value) / total}%`,
              height: "0.8rem",
            }}
          />
          <div
            style={{
              width: `${100 - (100 * value) / total}%`,
              opacity: 0.1,
            }}
            className="bg-white"
          />
        </Row>
      </Col>
      <Col xs="auto" className="text-warning fs-5 d-flex align-items-center">
        <p style={{ width: "2rem" }} className="m-0">
          {`${value < 10 ? "0" : ""}${value}`}
        </p>
      </Col>
    </Row>
  );
}

RoleManageAttribute.propTypes = {
  title: PropTypes.string.isRequired,
  color: PropTypes.string.isRequired,
  value: PropTypes.number,
  total: PropTypes.number.isRequired,
  className: PropTypes.string,
};

RoleManageAttribute.defaultProps = {
  value: 0,
  className: "",
};

export default RoleManageAttribute;
