const MEDIA_ICON = [
  { alt: "medium", icon: MediaMedium, href: "https://e-workcity.medium.com/" },
  { alt: "telegram", icon: MediaTelegram, href: "https://t.me/E_WorkCity" },
  { alt: "twitter", icon: MediaTwitter, href: "https://twitter.com/E_WorkCity" },
  { alt: "youtube", icon: MediaYoutube, href: "https://www.youtube.com/channel/UCxiIf2vT3MexCOZ805dmCmQ" },
  { alt: "white paper", icon: WhitePaper, href: "https://eworkcity.gitbook.io/e-work-city-wiki/" },
];

const LINK = [
  { to: "/home", text: "HOME" },
  { to: "/role", text: "Role" },
  { to: "", text: "Introduce" },
  { to: "", text: "NEWS" },
];

function getMediaIcon(index) {
  let className = "px-1";
  if (index === 0) {
    className = "pe-1";
  } else if (index === 4) {
    className = "ps-3";
  }

  const { icon, href } = MEDIA_ICON[index];

  return (
    <Col xs="auto" className={className}>
      <a
        href={href}
        rel="noreferrer"
        target="_blank"
        className="text-decoration-none ms-2 NavigationBar__Link"
      >
        {icon}
        {index === 4 && <span className="ms-2">E-Work City Wiki</span>}
      </a>
    </Col>
  );
}

function getLink(index) {
  const { to, text } = LINK[index];
  if (to === "") {
    const tooltip = <Tooltip className="fs-5">Coming soon</Tooltip>;
    return (
      <Col xs="auto" className="ps-4">
        <OverlayTrigger placement="top" overlay={tooltip}>
          <button type="button" className="NavigationBar__Link button-invisible fw-bold p-0">
            {text}
          </button>
        </OverlayTrigger>
      </Col>
    );
  }
  return (
    <Col xs="auto" className="ps-4 fw-bold">
      <NavLink
        to={to}
        className="NavigationBar__Link text-decoration-none"
        activeClassName="NavigationBar__Link--actived"
      >
        {text}
      </NavLink>
    </Col>
  );
}

function NavigationBar() {
  const [show, setShow] = useState(false);

  return (
    <Navbar
      fixed="bottom"
      expand="xl"
      variant="dark"
      className="NavigationBar fw-bold justify-content-end"
    >
      <Row className="mx-0 align-items-center w-100 flex-nowrap">
        <Col xs="auto" className="p-0 d-xl-none">
          <button
            type="button"
            className="button-invisible fw-bold text-white text-uppercase d-flex align-items-center p-0"
            onClick={() => setShow(!show)}
          >
            <img
              src={show ? CloseIcon : MenuIcon}
              alt={show ? "close" : "menu"}
              style={{ width: "0.8rem", marginRight: "0.1rem" }}
            />
            <span style={{ paddingTop: "0.25rem" }}>{show ? "Close" : "Menu"}</span>
          </button>
        </Col>
        <Col xs xl="auto" className="ps-4 pe-2">
          <Row className="justify-content-center">
            <Col xs="auto">
              <Link to="/home">
                <img src={HomeLogo} alt="logo" className="NavigationBar__Logo w-100" />
              </Link>
            </Col>
          </Row>
        </Col>
        <Col xs="auto" className="d-none d-xl-flex">
          {getMediaIcon(0)}
          {getMediaIcon(1)}
          {getMediaIcon(2)}
          {getMediaIcon(3)}
          {getMediaIcon(4)}
        </Col>
        <Col className="d-none d-xl-flex" />
        <Col xs="auto" className="d-none d-xl-flex">
          {getLink(0)}
          {getLink(1)}
        </Col>
        <Col xs="auto" className="d-none d-xl-flex">
          <WoodenButton color="black">
            Enter E-WORK CITY
          </WoodenButton>
        </Col>
        <Col
          xs="auto"
          className="fs-sm text-nowrap p-0 d-flex align-items-center text-white"
        >
          <img
            src={ConnectWalletIcon}
            alt=""
            style={{ width: "0.8rem", marginRight: "0.1rem" }}
          />
          Connect Wallet
        </Col>
      </Row>
      <Navbar.Offcanvas show={show} backdrop={false}>
        <Offcanvas.Body className="text-white NavigationBar__Offcanvas">
          <div className="NavigationBar__LogoHolder d-none d-xl-block" />
          <Row
            className="
              align-items-center
              justify-content-lg-between
              justify-content-center
              pt-2
              pt-xl-0
              flex-grow-1"
          >
            <Col xs="auto">
              <Row>
                {getLink(0)}
                <Col xs="12">
                  <hr className="hr-solid" />
                </Col>
                {getLink(1)}
                <div className="w-100" />
              </Row>
              <Row className="NavigationBar__OffcanvasBlock">
                <Col xs="auto">
                  <WoodenButton color="black">
                    Enter E-WORK CITY
                  </WoodenButton>
                </Col>
              </Row>
              <Row className="NavigationBar__OffcanvasBlock">
                {getMediaIcon(0)}
                {getMediaIcon(1)}
                {getMediaIcon(2)}
                {getMediaIcon(3)}
                {getMediaIcon(4)}
              </Row>
            </Col>
          </Row>
        </Offcanvas.Body>
      </Navbar.Offcanvas>
    </Navbar>
  );
}

export default NavigationBar;
