import React from "react";

import { Switch, Route, Redirect } from "react-router-dom";

import Home from "./views/Home/Home";
import Role from "./views/Role/Role";
import RoleCreate from "./views/RoleCreate/RoleCreate";
import RoleManage from "./views/RoleManage/RoleManage";

function Routes() {
  return (
    <Switch>
      <Route path="/role/manage">
        <RoleManage />
      </Route>
      <Route path="/role/create">
        <RoleCreate />
      </Route>
      <Route path="/role">
        <Role />
      </Route>
      <Route path="/home">
        <Home />
      </Route>
      <Route path="/">
        <Redirect to="/home" />
      </Route>
    </Switch>
  );
}

export default Routes;
