import "./RoleCreate.scss";

import React, { useState } from "react";
import { useHistory } from "react-router-dom";

import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import BorderFrame from "../../components/BorderFrame/BorderFrame";
import NavigationBar from "../../components/NavigationBar/NavigationBar";
import WoodenButton from "../../components/WoodenButton/WoodenButton";

import RoleCreateInfoInput from "./RoleCreateInfoInput/RoleCreateInfoInput";

import TextContainer from "../../components/TextContainer/TextContainer";

import RoleMale from "../../assets/role-male.png";
import RoleFemale from "../../assets/role-female.png";

function getRoleCard(type, selected, setter) {
  const text = type === 0 ? "Man" : "Woman";

  const variant = type === selected ? 3 : 0;
  const buttonText = type === selected ? "selected" : "choose";

  const image = type === 0 ? RoleMale : RoleFemale;

  return (
    <div
      className={`
        position-relative
        RoleCreate__Dialog
        ${selected === type && "RoleCreate__Selected"}
      `}
    >
      <img src={image} alt={type === 0 ? "male" : "female"} className="w-100" />
      <div
        className={`
          RoleCreate__DialogContent
          flex-grow-1
          d-flex
          flex-column
          justify-content-between
          align-items-scretch
        `}
      >
        <TextContainer bg="warning">{text}</TextContainer>
        <div className="d-flex flex-column align-items-center RoleCreate__ChooseDialog p-3">
          <p className="mb-0">
            <span className="text-white me-2">Price:</span>
            <span className="text-warning fs-2">188 </span>
            <span className="text-info">USDT</span>
          </p>
          <WoodenButton variant={variant} onClick={() => setter(type)} className="px-4">
            {buttonText}
          </WoodenButton>
        </div>
      </div>
    </div>
  );
}

function RoleCreate() {
  const [selectedRole, setSelectedRole] = useState(null);

  const history = useHistory();
  function routeTo(path) {
    return () => { history.push(path); };
  }

  return (
    <div className="RoleCreate d-flex flex-column">
      <NavigationBar />
      <BorderFrame />
      <div className="justify-content-center RoleCreate__Title--xs d-flex d-lg-none">
        <h1 className="fw-bold text-white">Creating Role</h1>
      </div>
      <div className="justify-content-center RoleCreate__Title d-none d-lg-flex">
        <h1 className="fw-bold text-white">Creating Role</h1>
      </div>
      <div className="d-flex align-items-center pb-4 flex-grow-1 px-3">
        <Container>
          <Row className="justify-content-center align-items-stretch pt-4">
            <Col xs="6" lg="4" xl="3" className="px-3">
              {getRoleCard(0, selectedRole, setSelectedRole)}
            </Col>
            <Col xs="6" lg="4" xl="3" className="px-3">
              {getRoleCard(1, selectedRole, setSelectedRole)}
            </Col>
            <Col xs="12" lg="4" xl="3" className="d-flex align-items-stretch mt-2 mt-lg-0 px-3">
              <Row className="RoleCreate__InfoDialog mx-0">
                <Col xs="6" className="text-uppercase text-white fw-bold fs-5">
                  Name
                  <RoleCreateInfoInput value="??" />
                </Col>
                <Col xs="6" className="text-uppercase text-white fw-bold fs-5">
                  Eyes
                  <RoleCreateInfoInput value="??" />
                </Col>
                <Col xs="6" className="text-uppercase text-white fw-bold fs-5 mt-2">
                  Weight
                  <RoleCreateInfoInput value="??" />
                </Col>
                <Col xs="6" className="text-uppercase text-white fw-bold fs-5 mt-2">
                  Height
                  <RoleCreateInfoInput value="??" />
                </Col>
                <Col xs="6" className="text-uppercase text-white fw-bold fs-5 mt-2">
                  City
                  <RoleCreateInfoInput value="E-Work" hasDice={false} />
                </Col>
                <Col xs="12" className="text-dark mt-4 opacity-50">
                  Create a character during the closed beta and you will get 3
                  random costumes.
                  <br />
                  (ShirtPantsShoe)
                </Col>
                <Col xs="12" className="mt-3">
                  <Button
                    variant={selectedRole === null ? "outline-dark" : "outline-warning"}
                    className="fw-bold w-100"
                    onClick={routeTo("/role/manage")}
                    disabled={selectedRole === null}
                  >
                    Create
                  </Button>
                </Col>
              </Row>
            </Col>
            <div className="RoleCreate__NavPlaceHolder" />
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default RoleCreate;
